import React, { useState, useEffect } from 'react';
import { Route, useHistory, Switch, Redirect } from 'react-router-dom';
import { api } from '../utils/api';
import Header from './Header.js';
import Main from './Main.js';
import Footer from './Footer.js';
import ImagePopup from './ImagePopup';
import EditProfilePopup from './EditProfilePopup';
import EditAvatarPopup from './EditAvatarPopup';
import AddPlacePopup from './AddPlacePopup';
import InfoTooltip from './InfoTooltip';
import ConfirmDeletePopup from './ConfirmDeletePopup';
import { CurrentUserContext } from '../contexts/CurrentUserContext';
import Login from './Login.js';
import Register from './Register.js';
import ProtectedRoute from './ProtectedRoute';
import * as auth from '../utils/auth.js';
import MobileMenu from './MobileMenu';

function App() {
  const history = useHistory();
  const [isEditProfilePopupOpen, setProfilePopupOpen] = useState(false);
  const [isEditAvatarPopupOpen, setAvatarPopupOpen] = useState(false);
  const [isAddPlacePopupOpen, setPlacePopupOpen] = useState(false);
  const [isImagePopupOpen, setImagePopupOpen] = useState(false);
  const [isInfoTooltipOpen, setInfoTooltipOpen] = useState(false);
  const [selectedCard, setSelectedCard] = useState({});
  const [currentUser, setCurrentUser] = useState({});
  const [loggedIn, setLoggedIn] = useState(false);
  const [userEmail, setEmail] = useState('');
  const [loginPage, setLoginPage] = useState(true);
  const [isAuth, setIsAuth] = useState(true);
  const [isOpenMobileMenu, setOpenMobileMenu] = useState(false);
  const [isConfirmDeletePopup, setConfirmDeletePopup] = useState(false)

  const handleEditAvatarClick = () => {
    setAvatarPopupOpen(true);
  }

  const handleEditProfileClick = () => {
    setProfilePopupOpen(true);
  }

  const handleAddPlaceClick = () => {
    setPlacePopupOpen(true);
  }

  const handleCardClick = () => {
    setImagePopupOpen(true);
  }

  const handleDeleteCard = () => {
    setConfirmDeletePopup(true);
  }

  const closeAllPopups = () => {
    setProfilePopupOpen(false);
    setAvatarPopupOpen(false);
    setPlacePopupOpen(false);
    setImagePopupOpen(false);
    setInfoTooltipOpen(false);
    setConfirmDeletePopup(false);
    setIsAuth(true);
  }

  useEffect(() => {
    api.getUserInfo()
      .then((res) => {
        setCurrentUser(res);
      })
      .catch((err) => {
        console.log(err);
      });
  }, [])

  function handleUpdateUser({ name, about }) {
    api.setUser(name, about)
      .then((res) => {
        setCurrentUser(res)
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  function handleUpdateAvatar({ avatar }) {
    api.setAvatar(avatar)
      .then((res) => {
        setCurrentUser(res)
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  function handleAddPlace({ name, link }) {
    api.setCard(name, link)
      .then((res) => {
        setCards([res, ...cards])
        closeAllPopups();
      })
      .catch((err) => {
        console.log(err);
      });
  }

  const [cards, setCards] = useState([]);

  useEffect(() => {
    api.getInitialCards()
      .then((result) => {
        setCards(result);
      })
      .catch((err) => {
        console.log(err);
      });
  }, []);

  function handleCardLike({ id, likes }) {
    const isLiked = likes.some(i => i._id === currentUser._id);
    let likeMethod = '';
    isLiked ? likeMethod = api.deleteLike(id) : likeMethod = api.putLike(id);
    likeMethod
      .then((newCard) => {
        setCards((state) => state.map((elem) => elem._id === id ? newCard : elem));
      })
      .catch((err) => {
        console.log(err);
      });
  }

  function handleCardDelete({ id }) {
    api.deleteCard(id)
      .then(() => {
        setCards((state) => state.filter((elem) => elem._id !== id))
      })
      .catch((err) => {
        console.log(err);
      })
  }


  function onRegister(data) {
    auth.register(data)
      .then((res) => {
        if (res) {
          setInfoTooltipOpen(true);
          history.push('/sign-in');
          setLoginPage(true);
        }
      })
      .catch((err) => {
        setIsAuth(false);
        setInfoTooltipOpen(true);
      });
  };

  function onLogin(data) {
    auth.authorize(data)
      .then((res) => {
        if (res.token) {
          localStorage.setItem('token', res.token);
          setEmail(data.email);
          setLoggedIn(true);
          history.push('/');
        }
      })
      .catch((err) => {
        setIsAuth(false);
        setInfoTooltipOpen(true);
      });
  };

  const tokenCheck = () => {
    const token = localStorage.getItem('token');
    if (!token) {
      return;
    }
    auth.getContent(token)
      .then((res) => {
        if (res) {
          setEmail(res.data.email);
          setLoggedIn(true);
          history.push('/');
        }
      })
      .catch((err) => {
        console.log(err);
      });
  }

  useEffect(() => {
    tokenCheck();
  }, [])

  const signOut = () => {
    localStorage.removeItem('token');
    setLoggedIn(false);
    history.push('/sign-in');
    setLoginPage(true);
  }

  function linkTo() {
    if (loginPage) {
      history.push('/sign-up');
      setLoginPage(false);
    } else {
      history.push('/sign-in');
      setLoginPage(true);
    }
  }

  function openMobileMenu() {
    isOpenMobileMenu ? setOpenMobileMenu(false) : setOpenMobileMenu(true);
  }

  return (
    <div className="page">
      <div className="page__container">
        <CurrentUserContext.Provider value={currentUser}>
          <MobileMenu loggedIn={loggedIn}
            email={userEmail}
            signOut={signOut}
            loginPage={loginPage}
            setLoginPage={setLoginPage}
            linkTo={linkTo}
            isOpenMobileMenu={isOpenMobileMenu}
          />
          <Header loggedIn={loggedIn}
            email={userEmail}
            signOut={signOut}
            loginPage={loginPage}
            setLoginPage={setLoginPage}
            linkTo={linkTo}
            openMobileMenu={openMobileMenu}
            isOpenMobileMenu={isOpenMobileMenu}
          />
          <Switch >
            <Route exact path="/sign-up">
              <Register onRegister={onRegister} setLoginPage={setLoginPage} />
            </Route>
            <Route exact path="/sign-in" >
              <Login onLogin={onLogin} />
            </Route>
            <ProtectedRoute exact path="/" component={Main} loggedIn={loggedIn}
              onEditProfile={handleEditProfileClick}
              onAddPlace={handleAddPlaceClick}
              onEditAvatar={handleEditAvatarClick}
              dataImage={setSelectedCard}
              openPopImage={handleCardClick}
              cards={cards}
              onCardLike={handleCardLike}
              onCardDelete={handleCardDelete}
              onConfirmDelete={handleDeleteCard}
            >
            </ProtectedRoute>
            <Route>
              {loggedIn ? <Redirect to="/" /> : <Redirect to="/sign-in" />}
            </Route>
          </Switch>
          <Footer />
            <AddPlacePopup
              isOpen={isAddPlacePopupOpen}
              onClose={closeAllPopups}
              onAddPlace={handleAddPlace}
            />
            <EditProfilePopup
              isOpen={isEditProfilePopupOpen}
              onClose={closeAllPopups}
              onUpdateUser={handleUpdateUser} />
            <ConfirmDeletePopup
              isOpen={isConfirmDeletePopup}
              onClose={closeAllPopups}
              onCardDelete={handleCardDelete}
            />
            <EditAvatarPopup
              isOpen={isEditAvatarPopupOpen}
              onClose={closeAllPopups}
              onUpdateAvatar={handleUpdateAvatar} />
            <ImagePopup
              card={selectedCard}
              onClose={closeAllPopups}
              isOpen={isImagePopupOpen} />
          <InfoTooltip
            isOpen={isInfoTooltipOpen}
            onClose={closeAllPopups}
            isAuth={isAuth}
          />
        </CurrentUserContext.Provider>
      </div>
    </div>
  );
}

export default App;
